import request from '../index.js'
// 
export const userLogin = (data) => {
	return request({
		url: "/api/user/login",
		method: "post",
		data: data,
	})
};

// 
export const userRegister = (data) => {
	return request({
		url: "/api/user/register",
		method: "post",
		data: data,
	})
};
// 
export const getUserInfo = () => {
	return request({
		url: "/api/user/userInfo",
		method: "get"
	})
};
// 
export const emailCode = (data) => {
	return request({
		url: "/api/user/emailCode",
		method: "post",
		data: data
	})
};

// 
export const updateAvatarAsync = (data) => {
	return request({
		url: "/api/person/updateAvatar",
		method: "put",
		data: data
	})
}

// 
export const updateSignature = (data) => {
	return request({
		url: "/api/person/updateSignature",
		method: "put",
		data: data
	})
}

// 
export const forgetPass = (data) => {
	return request({
		url: "/api/user/forgetPass",
		method: "put",
		data: data
	})
}