import store from "../store/index.js";
import ENV from '../config/index.js';
// 
const BASE_URL = ENV.serverAddress;
export const request = (options) => {
	let header = {};
	// 
	if (options.header) {
		if (options.header["Content-Type"]) {
			header['Content-Type'] = options.header["Content-Type"]
		} else {
			header['Content-Type'] = "application/json"
		}
	}
	// token
	const authorization = store.state.token;
	if (authorization) {
		header.authorization = authorization
	}
	return new Promise((resolve, reject) => {
		uni.request({
			url: BASE_URL + options.url,
			//
			data: options.data || {},
			// -token
			header: header,
			method: options.method || 'GET',

			// 
			success: (res) => {
				// token
				if (res.data.code === "T0001") {
					uni.removeStorageSync('token'); // token
					uni.navigateTo({
						url: "/pages/login/login"
					})
					// resolve(res.data);
				} else {
					// token
					resolve(res.data);
				}
			},
			// 
			fail: (error) => {
				uni.showToast({
					title: '',
					icon: "none",
					mask: true,
				})
				// 
				reject(error)
			}
		})
	})
}

export default request;
